import React, { Component } from 'react'

export default class NewsDiv extends Component {
    


    render() {
        let {title,url,urlToImage,discription} = this.props;
        return (
            <div className="card" style={{ height:'35rem' }}>                    
                    <img src={!urlToImage?`https://th.bing.com/th?id=OIP.O21Q6ByDjlqd7OoD0LWpCwHaE8&w=306&h=204&c=8&rs=1&qlt=90&o=6&dpr=1.4&pid=3.1&rm=2`:urlToImage} style={{
                        height:"14rem",
                        objectFit:'cover'
                    }} className="card-img-top" alt="..." />
                    <div className="card-body">
                        <h5 className="card-title">{title}</h5>
                        <p className="card-text">{discription}<a  
                        target="_blank"
                        href={url}                        
                        style={{
                            color:'black',
                            fontFamily:'arial',
                            fontStyle:'bold',
                            cursor:"pointer"


                        }}>Read more</a></p>
                    </div>
                    <p style={{
                        paddingLeft:'0.4rem'
                    }}><i>Source :- {this.props.author}</i></p>
                    <p style={{
                        paddingLeft:'0.4rem'
                    }}><i>Published At :- {this.props.date && this.props.date.slice(0,10)}</i></p>
                </div>

        )
    }
}
