import { BrowserRouter, Routes, Route } from "react-router-dom";
import "./App.css";
import GetNews from "./component/GetNews";
import Navbar from "./component/Navbar";
import React, {Component} from "react";
require('dotenv').config()

export default class App extends Component {
  apiKey = process.env.REACT_APP_API_KEY;
  constructor() {
    super();
  }
  render() {
    console.log(this.apiKey)
    return(
      <div>
        <BrowserRouter>
          <Navbar></Navbar>
          <Routes>
            <Route
              exact
              path="/"
              element={
                <GetNews key={"general"} pageSize={20} category={"general"} country="in" apiKey={this.apiKey}/>
              }
              
            />
            <Route
              exact
              path="/home"
              element={
                <GetNews key={"general"} pageSize={20} category={"general"} country="in" />
              }
              
            />
            <Route
              exact
              path="/business"
              
              element={
                <GetNews key={"business"} pageSize={20} category={"business"} country="in" />
              }
            />
            <Route
              exact
              path="/entertainment"
              element={
                <GetNews
                 key="entertainment"
                  pageSize={20}
                  category={"entertainment"}
                  country="in"
                />
              }
            />
            <Route
              exact
              path="/health"
              
              element={
                <GetNews key={"health"} pageSize={20} category={"health"} country="in" />
              }
            />
            <Route
              exact
              path="/science"
              
              element={
                <GetNews key={"science"} pageSize={20} category={"science"} country="in" />
              }
            />
            <Route
              exact
              path="/sports"
              
              element={
                <GetNews key={"sports"} pageSize={20} category={"sports"} country="in" />
              }
            />
            <Route
              exact
              path="/technology"
              
              element={
                <GetNews key={"technology"} pageSize={20} category={"technology"} country="in" />
              }
            />
          </Routes>
        </BrowserRouter>
      </div>
    );
  }
}
