import React, { Component } from 'react'
import NewsDiv from './NewsDiv';
import './GetNews.css'
import Loading from './Loading';
import PropTypes from 'prop-types'
import InfiniteScroll from "react-infinite-scroll-component";

export default class GetNews extends Component {
    static defaultProps = {
        country: 'in',
        pageSize: 20
    }

    static propTypes = {
        country: PropTypes.string
    }

    constructor(props) {
        super(props);
        this.state = {
            articles: "",
            page: 1,
            loading: true,
            totalResults: 0
        }
        document.title = this.props.category;
    }

    async componentDidMount() {
        let url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=${this.props.apiKey}&page=1&pageSize=${this.props.pageSize}`
        let data = await fetch(url)
        let parseData = await data.json()
        this.setState({
            articles: parseData.articles,
            page: 1,
            totalResults: parseData.totalResults,
            loading: false
        })
    }
    fetchMoreData = async () => {
        // this.setState({
        //     page:this.state.page+1
        // })
        const url = `https://newsapi.org/v2/top-headlines?country=${this.props.country}&category=${this.props.category}&apiKey=${this.props.apiKey}&page=${this.state.page + 1}&pageSize=${this.props.pageSize}`
        this.setState({
            loading: true
        })
        let data = await fetch(url)
        let parseData = await data.json()
        this.setState({
            articles: this.state.articles.concat(parseData.articles),
            loading: false,
            page: this.state.page + 1
        })
    };

    render() {
        return this.state.articles?(<>

            <h1 style={{
                textAlign:'center'
            }} className="my">My News App on {this.props.category} Headlines </h1>
            {this.state.loading && <Loading />}
            <InfiniteScroll
                dataLength={this.state.articles}
                next={this.fetchMoreData}
                hasMore={this.state.articles.length !== this.state.totalResults}
                loader={<Loading />}
            >

                <div className="container">
                    <div className="row">
                        {this.state.articles && this.state.articles.map((element) =>
                            <div className="col-md-3 my-2" key={element.url}>
                                <NewsDiv title={element.title}
                                    url={element.url}
                                    urlToImage={element.urlToImage} discription={
                                        (element.description === null ? "" :
                                            (element.description.length < 20 ? element.description : element.description.slice(0, 120) + "......"))
                                    }
                                    author={!element.source.name ? 'Unknown' : element.source.name}
                                    date={element.publishedAt} />
                            </div>
                        )}
                    </div>
                </div>

            </InfiniteScroll>
        </>):(<Loading/>)
    }
}

